import React, { Component } from "react";
import { compose } from "redux";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import moment from "moment";

// import component
import Button from "../../../../components/uielements/button";
import CardWrapper, {
  Box,
  StatusBadge,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import ModalUnprocessedPriceList from "./modalUnprocessedPriceList";
import PageHeader from "../../../../components/utility/pageHeader";
// import Popconfirms from "../../../../components/feedback/popconfirm";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import notification from "../../../../components/notification";
import {
  Col,
  Collapse,
  Empty,
  Icon,
  Input,
  Modal,
  Row,
  Select,
  Tooltip
} from "antd";

// import action
import actions from "../../../../redux/promotion/supplierPriceList/actions";
import getLanguage from "../../../App/MyLanguange";

const confirm = Modal.confirm;
const Option = Select.Option;
const Panel = Collapse.Panel;
const Search = Input.Search;

class SupplierPriceList extends Component {
  state = {
    filter: {
      id_location: null,
      id_supplier: null,
      id_supplier_pricelist: null,
      id_supplier_type: null,
      status: null,
      pagination: null
    },
    search: null,
    visibleModalUnprocessedPriceList: false,
    viewedPL: false
  };

  componentDidMount() {
    const { getSupplierPriceLists } = this.props;
    getSupplierPriceLists();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.supplierPriceList.error) {
      notification("error", this.props.supplierPriceList.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.supplierPriceList.success) {
      notification("success", this.props.supplierPriceList.success);
      this.props.clearSuccess();
    }

    const user = this.props.auth.user;
    let { permissions } = user ? user : [];
    const { pricelist_info } = this.props.supplierPriceList;

    if (
      pricelist_info &&
      !this.state.viewedPL &&
      permissions.includes("Confirm Supplier Pricelist")
    ) {
      this.showPriceList();
      this.setState({ viewedPL: true });
    }
  }

  // Inisiasi kolom tambahan untuk edit Supplier Price List
  listAction = {
    title: "",
    dataIndex: "id_supplier_pricelist",
    rowKey: "id_supplier_pricelist",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        {/* Update tooltip */}
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>
      </div>
    )
  };

  clearFilterState = () => {
    this.setState({
      filter: {
        id_location: null,
        id_supplier: null,
        id_supplier_pricelist: null,
        id_supplier_type: null,
        status: null,
        pagination: null
      }
    });
  };

  clearFilterSearchState = () => {
    this.setState({
      filter: {
        id_location: null,
        id_supplier: null,
        id_supplier_pricelist: null,
        id_supplier_type: null,
        status: null,
        pagination: null
      },
      search: null
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getSupplierPriceLists } = this.props;
    //const { current_page } = this.props.purchaseReceipt;
    const { filter } = this.state;
    let string = "";

    Object.keys(filter).forEach((key, index) => {
      if (filter[key]) {
        string
          ? (string += "&" + key + "=" + filter[key])
          : (string += key + "=" + filter[key]);
      }
    });

    this.setState({ search: null });
    getSupplierPriceLists(1, null, string);
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "id_location") {
      this.props.getSuppliersBasedOnLocation(value);
      this.setState({
        filter: {
          ...this.state.filter,
          id_supplier: null,
          [name]: value
        }
      });
    } else if (name === "id_supplier") {
      const { id_location } = this.state.filter;
      this.props.getSuppliersBasedOnLocation(id_location);
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getSupplierPriceLists } = this.props;
    const { search, filter } = this.props.supplierPriceList;
    getSupplierPriceLists(page, search, filter);
  };

  // Handle action di table
  handleRecord = (action, id) => {
    // console.log(action, data);
    if (action === "activate") {
      // data.is_active = 1;
      this.props.activateSupplierPriceList(id);
    } else {
      // data.is_active = 0;
      this.props.deactivateSupplierPriceList(id);
    }
  };

  // Handle Reset Filter and Search
  handleResetFilterSearch = () => {
    const { getSupplierPriceLists } = this.props;
    this.clearFilterSearchState();
    getSupplierPriceLists();
  };

  handleSearch = value => {
    const { getSupplierPriceLists } = this.props;
    this.clearFilterState();
    getSupplierPriceLists(1, value);
  };

  handleSearchSupplier = value => {
    const { id_location } = this.state.filter;
    const { getSuppliersBasedOnLocation } = this.props;
    getSuppliersBasedOnLocation(id_location, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  handleModalUnprocessedPriceList = () => {
    const { visibleModalUnprocessedPriceList } = this.state;
    visibleModalUnprocessedPriceList
      ? this.setState({ visibleModalUnprocessedPriceList: false })
      : this.setState({ visibleModalUnprocessedPriceList: true });
  };

  showPriceList = () => {
    const { pricelist_info } = this.props.supplierPriceList;

    confirm({
      title: pricelist_info.title,
      content: pricelist_info.content,
      // "There is one or more unprocessed pricelist. Do you want to process?",
      icon: <Icon type="info-circle" />,
      okText: pricelist_info.ok_button,
      cancelText: pricelist_info.cancel_button,
      onOk: () => {
        this.handleModalUnprocessedPriceList();
      },
      onCancel() {}
    });
  };

  render() {
    // --- check api GET
    // console.log(this.props.supplierPriceList);
    // console.log(this.props.auth);

    // --- ambil data dari state global:supplierPriceList

    const {
      pricelist_info,
      supplierPriceLists,
      suppliers,
      supplier_types,
      locations,
      loadingForeign,
      loadingGetCP,
      loadingSuppliers
    } = this.props.supplierPriceList;
    // console.log(loading, " ", loadingForeign);

    // console.log(suppliers);

    // --- init variable
    const dataSource = [];

    // --- ambil data user dari state global:auth
    const user = this.props.auth.user;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // --- Pagination Setup
    const { current_page, page_size, total } = this.props.supplierPriceList;

    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "pricelist_code",
        rowKey: "pricelist_code",
        width: "15%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "ordered";
              break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }
          return (
            <div style={{ display: "flex" }}>
              <StatusBadge className={className} />
              {text}
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "priceListType" }),
        dataIndex: "id_supplier_type",
        rowKey: "id_supplier_type",
        width: "12%",
        render: text => (
          <div>
            {text ? (
              <span>
                {<IntlMessages id="sidebar.supplierType" />}
                {/* <Badge
                  style={{ backgroundColor: "#87d068" }}
                  count="Supplier Type"
                />{" "} */}
              </span>
            ) : (
              <span>
                {<IntlMessages id="supplierCode" />}
                {/* <Badge style={{ backgroundColor: "#2db7f5" }} count="Supplier" />{" "} */}
              </span>
            )}
          </div>
        )
      },
      {
        title: getLanguage({ id: "sidebar.supplier" }),
        dataIndex: "supplier.supplier_name",
        rowKey: "supplier.supplier_name",
        width: "15%",
        render: (text, record) => (
          <div>
            {text ? (
              <span>{text} </span>
            ) : (
              <span>
                {record.supplier_type
                  ? record.supplier_type.supplier_type_name
                  : ""}{" "}
              </span>
            )}
          </div>
        )
      },
      {
        title: getLanguage({ id: "startDate" }),
        dataIndex: "start_date",
        rowKey: "start_date",
        width: "12%",
        render: text => {
          return <span>{moment(text).format("DD MMMM YYYY")}</span>;
        }
      },
      {
        title: getLanguage({ id: "endDate" }),
        dataIndex: "expired_date",
        rowKey: "expired_date",
        width: "12%",
        render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
      },

      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "20%"
      },
      // {
      //   title: getLanguage({id: "lastUpdate"}),
      //   dataIndex: "updated_at",
      //   rowKey: "updated_at",
      //   width: "20%",
      //   render: text => (
      //     <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
      //   )
      // },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by"
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({ id: "pending" });
              break;
            case 2:
              className = "ordered";
              display = getLanguage({ id: "confirmed" });
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "rejected" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "pending" });
              break;
          }
          return (
            // <div style={{ display: "flex" }}>
            <StatusTag className={className}>{display}</StatusTag>
            // </div>
          );
        }
      }
    ];

    // --- tampil kolom Update apabila user punya otorisasi untuk Update price List
    if (
      permissions.includes("Update Supplier Pricelist") &&
      columns.length === 8
    ) {
      columns.push(this.listAction);
    }

    // map data dari state global ke dataSource: Table
    Object.keys(supplierPriceLists).map((supplierPriceList, index) => {
      // console.log(supplier_type);
      // console.log(supplierPriceLists[supplierPriceList].id_supplier_type);
      return dataSource.push({
        ...supplierPriceLists[supplierPriceList],
        key: supplierPriceList
      });
    });

    // console.log(dataSource);

    return (
      <LayoutWrapper>
        {pricelist_info ? (
          <ModalUnprocessedPriceList
            history={this.props.history}
            handleModal={this.handleModalUnprocessedPriceList}
            supplierPriceList={this.props.supplierPriceList}
            visible={this.state.visibleModalUnprocessedPriceList}
          />
        ) : null}
        <Box>
          <PageHeader
            marginBottom={true}
            title={<IntlMessages id="supplierPriceLists.pageHeader" />}
          >
            {permissions.includes("Create Supplier Pricelist") ? (
              <Link to={`${this.props.match.path}/add`}>
                <Button
                  loading={loadingForeign}
                  disabled={loadingForeign}
                  type="primary"
                  className="mateAddInvoiceBtn"
                >
                  <IntlMessages id="supplierPriceLists.button.add" />
                </Button>
              </Link>
            ) : null}
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "code" }) +
                ", " +
                getLanguage({ id: "sidebar.supplier" }) +
                ", " +
                getLanguage({ id: "sidebar.location" }) +
                ", " +
                getLanguage({ id: "createdBy" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>
            <div
              style={{
                // width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetFilterSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          {/* Filter */}
          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    {locations ? (
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_location"
                        )}
                        value={
                          this.state.filter.id_location
                            ? this.state.filter.id_location
                            : []
                        }
                        style={{ width: "95%" }}
                        placeholder={
                          <IntlMessages id="purchaseReceipts.filter.select.location" />
                        }
                        optionFilterProp="children"
                        filterOption={false}
                        loading={loadingForeign}
                        allowClear={true}
                        //showArrow={false}
                      >
                        {locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ))}
                      </Select>
                    ) : null}
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="sidebar.supplierType" />
                    </label>
                    <br />
                    {supplier_types ? (
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_supplier_type"
                        )}
                        value={
                          this.state.filter.id_supplier_type
                            ? this.state.filter.id_supplier_type
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="supplierPriceLists.supplierType.select" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        //showArrow={false}
                      >
                        {supplier_types.map((data, key) => (
                          <Option key={key} value={data.id_supplier_type}>
                            {`${data.supplier_type_code} (${data.supplier_type_name})`}
                          </Option>
                        ))}
                      </Select>
                    ) : null}
                  </Col>
                </Row>
                <Row
                  justify="start"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="suppliers.pageHeader" />
                    </label>
                    <br />
                    {suppliers ? (
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_supplier"
                        )}
                        value={
                          this.state.filter.id_supplier
                            ? this.state.filter.id_supplier
                            : []
                        }
                        style={{ width: "95%" }}
                        placeholder={
                          <IntlMessages id="purchaseOrders.filter.select.supplier" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign || loadingSuppliers}
                        allowClear={true}
                        notFoundContent={
                          this.state.filter.id_location ? (
                            <Empty description={<IntlMessages id="noData" />} />
                          ) : (
                            <Empty
                              description={<IntlMessages id="noLocation" />}
                            />
                          )
                        }
                        onSearch={this.handleSearchSupplier}
                      >
                        {suppliers.map((data, key) => {
                          if (
                            this.state.filter.id_location === data.id_location
                          ) {
                            return (
                              <Option key={key} value={data.id_supplier}>
                                {`${data.supplier_code} (${data.supplier_name})`}
                              </Option>
                            );
                          }
                        })}
                      </Select>
                    ) : null}
                  </Col>
                  <Col span={3}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : page_size
                      }
                      style={{ width: "90%" }}
                      optionFilterProp="children"
                      filterOption={true}
                      //allowClear={true}
                      //showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </Col>
                  <Col
                    span={4}
                    style={{
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilterSearch} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>
          {/* /Filter */}

          <CardWrapper>
            {supplierPriceLists.length === 0 &&
            !this.props.supplierPriceList.loadingGetCP ? (
              <Empty
                description={
                  <IntlMessages id="supplierPriceLists.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loadingGetCP}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    supplierPriceList: state.supplierPriceList,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(SupplierPriceList);
