const actions = {
  getSupplierPriceLists: (page, search, filter) => ({
    type: "GET_SUPPLIER_PRICE_LISTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createSupplierPriceList: () => ({
    type: "CREATE_SUPPLIER_PRICE_LIST_REQUEST"
  }),
  getItemsForSupplierPriceList: (id_location, search) => ({
    type: "GET_FILTER_ITEMS_FOR_SUPPLIER_PRICE_LIST_REQUEST",
    payload: { id_location: id_location, search: search }
  }),
  getSuppliersBasedOnLocation: (id_location, search) => ({
    type: "GET_SUPPLIERS_FOR_SUPPLIER_PRICE_LIST_REQUEST",
    payload: { id_location: id_location, search: search }
  }),
  storeSupplierPriceList: data => ({
    type: "STORE_SUPPLIER_PRICE_LIST_REQUEST",
    payload: { body: data }
  }),
  editSupplierPriceList: id_supplier_pricelist => ({
    type: "EDIT_SUPPLIER_PRICE_LIST_REQUEST",
    payload: { id_supplier_pricelist: id_supplier_pricelist }
  }),
  updateSupplierPriceList: (id_supplier_pricelist, data) => ({
    type: "UPDATE_SUPPLIER_PRICE_LIST_REQUEST",
    payload: {
      id_supplier_pricelist: id_supplier_pricelist,
      body: data
    }
  }),
  deactivateSupplierPriceList: id_supplier => ({
    type: "DEACTIVATE_SUPPLIER_PRICE_LIST_REQUEST",
    payload: { id_supplier }
  }),
  activateSupplierPriceList: id_supplier => ({
    type: "ACTIVATE_SUPPLIER_PRICE_LIST_REQUEST",
    payload: { id_supplier }
  }),
  rejectSupplierPriceList: (id_supplier, data) => ({
    type: "REJECT_SUPPLIER_PRICE_LIST_REQUEST",
    payload: { id_supplier, body: data }
  }),
  confirmSupplierPriceList: id_supplier => ({
    type: "CONFIRM_SUPPLIER_PRICE_LIST_REQUEST",
    payload: { id_supplier }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};
export default actions;
